.board.test {
  position: absolute;
  left: -320px;
  width: 300px;
  display: flex;
  flex-wrap: wrap;
  margin: 15px 0;
}
.board.test .cell {
  aspect-ratio: 1 / 1;
  position: relative;
  width: 12.5%;
  height: 12.5%;
  display: flex;
  justify-content: center;
  align-items: center;
  opacity: 0.4;
}
.board.test .cell img {
  width: 90%;
  height: 90%;
  position: relative;
  z-index: 1;
  opacity: 0;
}
.board.test .enPassant,
.board.test .toWhoCheckmate,
.board.test .toWhoCheck {
  position: absolute;
  width: 100%;
  height: 100%;
  background-color: rgb(72, 255, 81);
  opacity: 0.5;
}
.board.test .toWhoCheckmate {
  background-color: rgb(121, 72, 255);
}
.board.test .toWhoCheck {
  background-color: rgb(255, 72, 72);
}
.board.test .white {
  background-color: #ffffff;
}
.board.test .black {
  background-color: #ebebeb;
}
.available {
  width: 30%;
  height: 30%;
  opacity: 0.15;
  background-color: black;
  border-radius: 50%;
  position: absolute;
}
.board.test .availableToTake {
  width: 100%;
  height: 100%;
  background-color: transparent;
  opacity: 0.15;
  border-radius: 50%;
  position: absolute;
  box-shadow: 0px 0px 0px 5px black inset;
}
.board.test .cell:nth-child(16n + 1)::before {
  content: "";
}
.board.test .cell:nth-child(16n + 9)::before {
  content: "";
}
.board.test .cell:nth-last-child(-n + 8)::after {
  content: "";
}
